<template>
  <v-app id="login" class="indigo">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm6 md3 lg3>
            <v-card class="elevation-1 pa-3">
              <v-card-text>
                <div class="layout column align-center">
                  <img
                    :src="require('@/assets/m.png')"
                    alt="yescare"
                    width="120"
                    height="120"
                  />
                  <h1 class="flex my-1 indigo--text">Yescare Authentification</h1>
                </div>
                <v-form
                  ref="form"
                  v-model="valid"
                  lazy-validation
                  >
                  <v-text-field
                    append-icon="person"
                    name="login"
                    label="Username"
                    type="text"
                    v-model="username"
                    :error="error"
                    :rules="[rules.requiredUsername]"
                    required
                  ></v-text-field>
                  <v-text-field
                    :append-icon="showpassword ? 'visibility_off' : 'visibility'"
                    :type="showpassword ? 'text' : 'password'"
                    :rules="[rules.required]"
                    name="input-10-2"
                    label="Password"
                    class="input-group--focused"
                    :error="error"
                    :error-messages="message"
                    @click:append="showpassword = !showpassword"
                    @keyup.enter="submitLogin(); load =true"
                    v-model="password"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <router-link color="indigo" :to="{name: 'daftarPasien'}" tag="button"  class="navbar-item ">Daftar Sekarang</router-link>
                  <v-btn color="indigo" :loading="load" dark @click.prevent="submitLogin(); load =true">
                    Login
                  </v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>
<script>
import store from '../../store'
export default {
  data () {
    return {
      error: false,
      showpassword: false,
      message: '',
      username: localStorage.getItem('username'),
      password: '',
      loginError: false,
      load: false,
      valid: true,
      rules: {
        required: value => !!value || 'Password tidak boleh kosong',
        requiredUsername: value => !!value || 'Username tidak boleh kosong',
        usernamematch: () => ('Username atau password yang anda masukkan salah')
      }
    }
  },
  methods: {
    reset () {
      this.$refs.form.reset()
    },
    submitLogin () {
      this.loginError = false
      this.$http.post(this.$apiUrl + 'auth/login', {
        username: this.username,
        password: this.password
      }).then(response => {
        store.commit('loginUser')
        localStorage.setItem('token', response.data.access_token)
        localStorage.setItem('roles', response.data.role)
        this.$router.push({ name: 'DashboardContent' })
      }).catch(error => {
        console.log(error)
        this.load = false
        this.error = error !== null
        this.message = `Username atau password tidak sesuai`
      })
    }
  }
}
</script>


